<template>
  <div class="login-container">
    <!-- 背景装饰 -->
    <div class="background-decoration">
      <div class="floating-shapes">
        <div class="shape shape-1"></div>
        <div class="shape shape-2"></div>
        <div class="shape shape-3"></div>
        <div class="shape shape-4"></div>
      </div>
    </div>

    <!-- 主要内容区域 -->
    <div class="login-content">
      <div class="login-card">
        <!-- 头部Logo区域 -->
        <div class="login-header">
          <div class="logo-container">
            <img :src="logo" alt="系统Logo" class="logo" />
          </div>
          <h1 class="welcome-title">欢迎登录</h1>
          <p class="welcome-subtitle">百亿四方支付管理系统</p>
        </div>

        <!-- 登录表单 -->
        <div class="login-form-container">
          <el-form 
            :model="loginForm" 
            class="login-form"
            label-position="top"
            size="large"
          >
            <div class="form-item">
              <el-form-item>
                <el-input
                  v-model="username"
                  placeholder="请输入登录账号"
                  class="custom-input"
                  clearable
                >
                  <template #prefix>
                    <el-icon class="input-icon"><UserFilled /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </div>

            <div class="form-item">
              <el-form-item>
                <el-input
                  v-model="password"
                  type="password"
                  placeholder="请输入登录密码"
                  class="custom-input"
                  show-password
                  clearable
                >
                  <template #prefix>
                    <el-icon class="input-icon"><Lock /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </div>

            <div v-if="showGoogleAuth" class="form-item">
              <el-form-item>
                <el-input
                  v-model="googleAuthCode"
                  placeholder="请输入谷歌验证码"
                  class="custom-input"
                  clearable
                >
                  <template #prefix>
                    <el-icon class="input-icon"><Lock /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </div>

            <div class="form-options">
              <el-checkbox v-model="rememberMe" class="remember-checkbox">
                保持会话
              </el-checkbox>
            </div>

            <div class="form-submit">
              <el-button
                type="primary"
                size="large"
                class="login-button"
                @click="loginData"
                :disabled="!username || !password"
                :loading="loginLoading"
              >
                <span v-if="!loginLoading">登录账号</span>
                <span v-else>登录中...</span>
              </el-button>
            </div>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue'
import { ElMessage } from 'element-plus'
import { login } from "@/api/login.ts"
import { UserFilled, Lock } from '@element-plus/icons-vue'
import logo from '@/assets/login-header.png'

// 响应式数据
const username = ref('')
const password = ref('')
const rememberMe = ref(false)
const googleAuthCode = ref('')
const showGoogleAuth = ref(true)
const loginLoading = ref(false)

// 表单数据
const loginForm = ref({
  username: '',
  password: '',
  googleAuthCode: '',
  rememberMe: false
})

const loginData = async () => {
  if (!username.value || !password.value) {
    ElMessage.error('请输入账号和密码')
    return
  }

  try {
    loginLoading.value = true
    
    const response = await login({
      username: username.value,
      password: password.value,
      google_code: showGoogleAuth.value ? googleAuthCode.value : '',
    })

    if (response.status) {
      ElMessage.success('登录成功')
      setTimeout(() => {
        window.location.href = '/dashboard'
      }, 1000)
    } else {
      ElMessage.error(response.message || '登录失败')
    }
  } catch (error) {
    ElMessage.error('登录失败，请重试')
    console.error('登录错误:', error)
  } finally {
    loginLoading.value = false
  }
}
</script>

